import React, { useState, useEffect } from 'react'
//import clsx from 'clsx'

import i18n from '../../../controller/i18n'
import Log from '../../../controller/Log'

import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'

import Dialog from '../../../components/Dialog'
import Inputs from '../../../components/Inputs'


/*
 * STYLES
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
}));
 */


/*
 *
 */
const ComponentConfig = React.memo(props => {
	Log._('WidgetPage', 'RENDER ComponentConfig', 'development');
	//const classes = useStyles();
	const { _onClose, _onChange, id, config } = props;
	
	const [open,setOpen] = useState(false);
	const onClose = () => {
		setOpen(false)
		_onClose();
	}
	
	useEffect(() => {
		setOpen(config !== null);
	}, [config]);
	
	if (id === null || config === null) {
		return null;
	}
	
	// component configuration
	const componentConfiguration = Object.keys(config).map(configKey => {
		let configOption = config[configKey];
		configOption = typeof configOption === 'object' ? configOption : { 'label': configKey, 'val': configOption.val !== undefined ? configOption.val : configOption }
		
		configOption.label = configOption.label || configKey;
		configOption.label = configOption._translate ? i18n.t(configOption.label) : configOption.label;
		configOption.val = configOption._translate ? i18n.t(configOption.val) : configOption.val;
		delete configOption._translate;
		
		const Tag = Inputs[configOption.type || 'Text'];
		return (
			<ListItem key={'Config-' + configKey} divider>
				<ListItemText primary={configOption.label} />
				<ListItemSecondaryAction>
					<Tag
						key={'componentConfig.' + id + '.' + configKey}
						id={'componentConfig.' + id + '.' + configKey}
						{...configOption}
						
						label={''}
						width={100}
						inputProps={{
							style: { textAlign: 'right' }
						}} 
						
						onChange={_onChange}
						/>
					
				</ListItemSecondaryAction>
			</ListItem>
		);
	});
	
	return (
		<Dialog
			header={i18n.t('Component configuration')}
			actions={
				<Inputs.Button key="action-save" variant="text" onClick={onClose} color="primary" label="Apply" />
			}
			
			dense
			maxWidth='xs'
			fullWidth={true}
			open={open}
			>
			
			<List dense disablePadding>
				{componentConfiguration}
			</List>
		</Dialog>
	);
});

export default ComponentConfig;
